/* Fonts */
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../fonts/Rubik-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Rubik-Medium.woff2") format("woff2"), url("../fonts/Rubik-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Rubik-Bold.woff2") format("woff2"), url("../fonts/Rubik-Bold.woff") format("woff");
  font-display: swap;
}
/* Normalize */
body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Container */
.container {
  max-width: 1620px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Page */
.page {
  font-family: "Rubik";
  font-weight: normal;
  font-size: 14px;
  line-height: 1.4;
  background: #fff;
  display: flex;
  flex-direction: column;
}

/* Popular goods */
.popular-goods {
  margin: 80px 0;
}
@media (max-width: 725px) {
  .popular-goods {
    margin: 40px 20px;
  }
}

.popular-goods__wrapper {
  max-width: 1600px;
  padding: 0 100px;
}
@media (max-width: 1630px) {
  .popular-goods__wrapper {
    padding: 15px;
  }
}
@media (max-width: 725px) {
  .popular-goods__wrapper {
    padding: 0;
  }
}

.popular-goods__header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

.popular-goods__title {
  font-family: "Rubik", sans-serif;
  font-size: 34px;
  font-weight: 700;
  line-height: 40px;
  text-transform: uppercase;
  color: #000;
  margin-right: 25px;
}
@media (max-width: 725px) {
  .popular-goods__title {
    font-size: 18px;
  }
}

.popular-goods__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
@media (max-width: 1000px) {
  .popular-goods__nav-list {
    flex-direction: column;
    align-items: flex-start;
  }
}

.popular-goods__nav-link {
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  line-height: 1.4;
  text-decoration: none;
  color: #535658;
  margin-right: 25px;
  position: relative;
}
.popular-goods__nav-link::after {
  content: "";
  display: block;
  border-bottom: 1px dotted #535658;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.popular-goods__nav-link:hover {
  color: #000;
}
@media (max-width: 725px) {
  .popular-goods__nav-link {
    font-size: 14px;
  }
}

/* Slider */
.slider {
  margin: 0 auto;
  position: relative;
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}
@media (max-width: 1465px) {
  .slider__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 1000px) {
  .slider__list {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}
@media (max-width: 725px) {
  .slider__list {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}

.slider__item {
  max-width: 335px;
}

.slider__button {
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1630px) {
  .slider__button {
    display: none;
  }
}

.slider__button--prev {
  left: -86px;
  top: 50%;
  transform: translateY(-50%);
}

.slider__button--next {
  right: -86px;
  top: 50%;
}

/* Card */
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #ecf0f4;
  border-radius: 5px;
  overflow: hidden;
}

.card__picture {
  margin-bottom: 10px;
  position: relative;
}

.card__image {
  max-width: 334px;
  height: auto;
  display: block;
  object-fit: contain;
  cursor: pointer;
}
@media (max-width: 725px) {
  .card__image {
    max-width: 260px;
  }
}

.card__pagination {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 725px) {
  .card__pagination {
    display: none;
  }
}

.card__pagination-button {
  width: 20px;
  height: 2px;
  border: none;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.card__pagination-button--active {
  background-color: #f1d264;
}

.card__title {
  font-family: "Rubik", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  text-align: center;
  color: #000;
  max-width: 260px;
  margin: 0 auto;
  margin-bottom: 7px;
}
@media (max-width: 725px) {
  .card__title {
    max-width: 220px;
  }
}

.card__price {
  margin-bottom: 7px;
}

.card__current-price {
  font-family: "Rubik", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 22px;
  color: #000;
  margin-right: 7px;
}

.card__old-price {
  font-family: "Rubik", sans-serif;
  font-size: 14px;
  line-height: 22px;
  color: #8d9295;
}

.card__control {
  display: flex;
  margin-bottom: 25px;
}

/* Control */
.control {
  width: 100px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border: 1px solid #ecf0f4;
  border-radius: 5px;
  margin-right: 10px;
}
@media (max-width: 725px) {
  .control {
    display: none;
  }
}

.control__button {
  width: 30px;
  height: 30px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.control__button--minus {
  background-image: url("../img/icons/minus.svg");
}
.control__button--plus {
  background-image: url("../img/icons/plus.svg");
}

.control__counter {
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  color: #4c4e50;
}

/* Button */
.button {
  width: 174px;
  height: 50px;
  border: none;
  outline: none;
  background-color: #f1d264;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background 0.2s linear;
}
.button:hover {
  background-color: #edc435;
}

.button__image {
  margin-right: 10px;
}

.button__text {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #000;
}